<template>
  <div>
    <Header></Header>

    <div class="buy-credits-container">

      <div class="credits-info row">
        <div class="col-sm-4 info-part">
          <h2 class="credits-title">Credits</h2>
          <div class="credits-number">
            <label id="userCredits">{{userCredits}} 100</label>
          </div>
        </div>
        <div class="col-sm-8 advice-part">
          Use your credit card to get more credits
        </div>
      </div>

      <div class="choose-title">Choose the option</div>

      <div class="buy-option row">
        <div class="col-sm-1 coin-img">
          <img src="../css/images/coin.png">
        </div>
        <div class="col-sm-9 buy-info">
          <div class="row">
            <div class="col-sm-6 buy-number">10 credits</div>
            <div class="col-sm-6 buy-sum">{{price[0]}}0.99 {{currencySymbol}}$</div>
          </div>
        </div>
        <div class="col-sm-2 btn-wrapper">
          <button id="buy10" type="submit" class="btn buy-btn" v-on:click="buyClick($event)">BUY</button>
        </div>
      </div>

      <div class="buy-option row">
        <div class="col-sm-1 coin-img">
          <img src="../css/images/coin2.png">
        </div>
        <div class="col-sm-9 buy-info">
          <div class="row">
            <div class="col-sm-6 buy-number">20 credits</div>
            <div class="col-sm-6 buy-sum">{{price[1]}}1.99 {{currencySymbol}}$</div>
          </div>
        </div>
        <div class="col-sm-2 btn-wrapper">
          <button id="buy20" type="submit" class="btn buy-btn" v-on:click="buyClick($event)">BUY</button>
        </div>
      </div>

      <div class="buy-option row">
        <div class="col-sm-1 coin-img">
          <img src="../css/images/coin3.png">
        </div>
        <div class="col-sm-9 buy-info">
          <div class="row">
            <div class="col-sm-6 buy-number">30 credits</div>
            <div class="col-sm-6 buy-sum">{{price[2]}}3.99 {{currencySymbol}}$</div>
          </div>
        </div>
        <div class="col-sm-2 btn-wrapper">
          <button id="buy30" type="submit" class="btn buy-btn" v-on:click="buyClick($event)">BUY</button>
        </div>
      </div>

      <div class="buy-option row">
        <div class="col-sm-1 coin-img">
          <img src="../css/images/coin4.png">
        </div>
        <div class="col-sm-9 buy-info">
          <div class="row">
            <div class="col-sm-6 buy-number">40 credits</div>
            <div class="col-sm-6 buy-sum">{{price[3]}}0.99 {{currencySymbol}}$</div>
          </div>
        </div>
        <div class="col-sm-2 btn-wrapper">
          <button id="buy40" type="submit" class="btn buy-btn" v-on:click="buyClick($event)">BUY</button>
        </div>
      </div>

    </div>


    <div class="card-footer footer">
      MEMOSEEDS INC., ALL RIGHTS RESERVED
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  import router from '../router'
  import Header from './Header'

  export default {
    name: "BuyCredits",
    components: {
      Header
    },
    data() {
      return {
        userName: "",
        userCredits: "",
        price: [],
        currency: "",
        currencySymbol: ""
      }
    },

    beforeCreate: function () {
      document.body.className = 'inside';

      //let cur = this.$cookies.get('country');
      let cur = "France";

      let config = {
        headers: {
          'Access-Control-Allow-Origin': '*',
          'Accept': 'application/json',
          'Content-Type': 'application/json',
          'Authorization': 'Bearer' + this.$cookies.get('user_session')
        }
      };

      axios.post('https://memeseeds.herokuapp.com/purchase/options', {"country": cur}, config)
        .then(response => {
          console.log(response.data);
          for (let i = 0; i < 4; i++) {
            this.price[i] = response.data.purchases[i].price.amount;
          }
          this.currency = response.data.purchases[0].price.currency;
        })
        .catch(error => {
          console.log(error);
          alert("Error occurred during operation. Please try again");
        });

    },
    created: function () {
      this.getUserInfo();
    },

    methods: {
      logOut: function () {
      },

      buyClick: function (event) {
        let targetId = event.currentTarget.id;
        let resAmount = 0;
        switch (targetId) {
          case 'buy10':
            resAmount = 10;
            break;
          case 'buy20':
            resAmount = 20;
            break;
          case 'buy30':
            resAmount = 30;
            break;
          case 'buy40':
            resAmount = 40;
            break;
        }


      },

      getUserInfo: function () {
        this.userCredits = this.$cookies.get('userCredits');
        this.userName = this.$cookies.get('userName');
      }

    }
  }
</script>

<style scoped>
  .credits-info {
    width: 100%;
    padding: 30px 40px;
    margin: 0;
  }

  .info-part {
    background: #acd8c7;
    width: 20%;
    color: #ffffff;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;
    text-align: center;
    align-content: center;
  }

  .credits-info .credits-title {
    margin-top: 7%;
    margin-bottom: 0;
    font-size: 1.7rem;
  }

  .credits-info .credits-number {
    color: #0b486d;
  }

  .credits-info .advice-part {
    color: #f5979a;
    text-align: right;
    background-color: #ffffff;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    padding-top: 30px;
  }

  .choose-title {
    width: 100%;
    padding-left: 10%;
    text-align: left;
    color: #2095a6;
    font-size: 15px;
    margin-bottom: 10px;
  }

  .buy-option {
    padding: 10px 100px;
    text-align: center;
    width: 100%;
  }

  .coin-img img {
    width: 35px;
    height: 35px;
  }

  .buy-option .buy-info {
    padding: 0 10px;
    background-color: white;
    color: black;
    margin: auto 25px;
    flex: 0 0 60%;
  }

  .buy-info .buy-number {
    text-align: left;
    padding-left: 30px;
  }

  .buy-info .buy-sum {
    text-align: right;
    padding-right: 30px;
  }

  .buy-option .btn-wrapper {
    margin: auto;
  }

  .buy-btn {
    background-color: #f59699 !important;
    border-radius: 20px;
    font-size: 13px;
    color: white;
    width: 130px;
    height: 27px;
    border-color: white;
    margin-left: -40px;
  }

  .btn-wrapper button:hover {
    background-color: #f56e72 !important;
    cursor: pointer;
    color: white;
  }

  /*************************************/

  .footer {
    text-align: center;
    background-color: #bebfc0;
    color: white;
    letter-spacing: 5px;
    position: absolute;
    width: 100%;
    bottom: 0;
  }
</style>



